export default {

	name: "latte-tab",

	inject: ["tabContainer"],

	props: {
		badge: {default: "", type: String},
		icon: {default: "", type: String},
		label: {default: "", type: String}
	},

	beforeDestroy()
	{
		if (!this.tabContainer)
			return;

		this.tabContainer.updateChildren();
		this.tabContainer.updateTabBars();
	},

	data()
	{
		return {
			active: false
		};
	},

	mounted()
	{
		if (!this.tabContainer)
			return;

		this.tabContainer.updateChildren();
		this.tabContainer.updateTabBars();
	},

	watch: {

		active()
		{
			this.$emit("active", this.active);
		}

	}

}

